	module.exports = {"data":{"markdownRemark":{"html":"<h2>Big Omega Notation</h2>\n<p>This is a stub. <a href='https://github.com/freecodecamp/guides/tree/master/src/pages/computer-science/notation/big-omega-notation/index.md' target='_blank' rel='nofollow'>Help our community expand it</a>.</p>\n<p><a href='https://github.com/freecodecamp/guides/blob/master/README.md' target='_blank' rel='nofollow'>This quick style guide will help ensure your pull request gets accepted</a>.</p>\n<!-- The article goes here, in GitHub-flavored Markdown. Feel free to add YouTube videos, images, and CodePen/JSBin embeds  -->\n<p>Similar to <a href=\"https://guide.freecodecamp.org/computer-science/notation/big-o-notation\">big O</a> notation, big Omega(Ω) function is used in computer science to describe the performance or complexity of an algorithm.  </p>\n<p>If a running time is Ω(f(n)), then for large enough n, the running time is at least k⋅f(n)  for some constant k. Here’s how to think of a running time that is Ω(f(n)):<br>\n<img src=\"https://s3.amazonaws.com/ka-cs-algorithms/Omega_fn.png\" alt=\"big-omega function\"/><br>\nWe say that the running time is “big-Ω of f(n).” We use big-Ω notation for <strong>asymptotic lower bounds</strong>, since it bounds the growth of the running time from below for large enough input sizes.  </p>\n<h3>Difference between Big O and Big Ω</h3>\n<p>The difference between Big O notation and Big Ω notation is that Big O is used to describe the worst case running time for an algorithm. But, Big Ω notation, on the other hand, is used to describe the best case running time for a given algorithm.</p>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<ul>\n<li><a href=\"https://www.khanacademy.org/computing/computer-science/algorithms/asymptotic-notation/a/big-big-omega-notation\">Big-Ω (Big-Omega) notation</a></li>\n<li><a href=\"http://www.youtube.com/watch?feature=player_embedded&v=OpebHLAf99Y\n\" target=\"_blank\"><img src=\"http://img.youtube.com/vi/OpebHLAf99Y/0.jpg\" \nalt=\"MYCODSCHOOL Time complexity analysis\" width=\"240\" height=\"180\" border=\"10\" /></a></li>\n</ul>","fields":{"slug":"/computer-science/notation/big-omega-notation/"},"frontmatter":{"title":"Big Omega Notation"}}},"pathContext":{"slug":"/computer-science/notation/big-omega-notation/"}}

